import db from '@/lib/db';
import { NextResponse } from 'next/server';

const ADMIN_PASSWORD = 'Admin@123';

// GET all users
export async function GET(request) {
    try {
        const authHeader = request.headers.get('authorization');
        if (!authHeader || !authHeader.startsWith('Bearer ')) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
        }
        const userId = authHeader.split(' ')[1];

        const requestingUser = db.prepare('SELECT role FROM users WHERE id = ?').get(userId);
        if (!requestingUser || requestingUser.role !== 'admin') {
            return NextResponse.json({ error: 'Forbidden' }, { status: 403 });
        }

        const users = db.prepare('SELECT id, student_id, name, role, password, created_at FROM users ORDER BY role DESC, created_at ASC').all();
        return NextResponse.json(users);
    } catch (error) {
        console.error('Error fetching users:', error);
        return NextResponse.json({ error: 'Internal Server Error' }, { status: 500 });
    }
}

// DELETE a user
export async function DELETE(request) {
    try {
        const authHeader = request.headers.get('authorization');
        const { password, userId } = await request.json();

        if (!authHeader || !authHeader.startsWith('Bearer ')) {
            return NextResponse.json({ error: 'Unauthorized' }, { status: 401 });
        }

        if (password !== ADMIN_PASSWORD) {
            return NextResponse.json({ error: 'Invalid admin password' }, { status: 403 });
        }

        if (!userId) {
            return NextResponse.json({ error: 'User ID is required' }, { status: 400 });
        }

        // Delete user's related data first to avoid foreign key issues
        db.prepare('DELETE FROM transactions WHERE user_id = ?').run(userId);
        db.prepare('DELETE FROM access_logs WHERE user_id = ?').run(userId);
        // Delete user
        const result = db.prepare('DELETE FROM users WHERE id = ?').run(userId);

        if (result.changes === 0) {
            return NextResponse.json({ error: 'User not found' }, { status: 404 });
        }

        return NextResponse.json({ message: 'User deleted successfully' });
    } catch (error) {
        console.error('Error deleting user:', error);
        return NextResponse.json({ error: 'Internal Server Error' }, { status: 500 });
    }
}
